<template>
  <div class="">
    <my-space-header
      :BackArrow="require('@/assets/images/spaces/back-arrow.svg')"
      :share="require('@/assets/images/spaces/share.svg')"
      :Logo="icon"
      :Title="name"
      :Members="members_count"
      InviteOnly="invite_only"
      YouHaveInvite="you_have_invite"
      NoBackGround="with-background"
      :BackGroundImage="thumb"
    ></my-space-header>
    <space-tab class="m-16 md:m-40">
      <div class="flex felx-wrap items-center">
        <IgButton
          class="ig-small ig-auto px-3 mx-3 mb-3"
          buttonName="home"
          :textColor="selectedTab == 'home' ? 'ig-white-text' : 'ig-gray-text'"
          :backgroundColor="selectedTab == 'home' ? 'ig-gray' : ''"
          SmallFontSize="ig-font-small"
          @click="changeTab('home')"
        />
        <IgButton
          class="ig-small ig-auto px-3 mx-3 mb-3"
          buttonName="postings"
          :textColor="
            selectedTab == 'postings' ? 'ig-white-text' : 'ig-gray-text'
          "
          :backgroundColor="selectedTab == 'postings' ? 'ig-gray' : ''"
          SmallFontSize="ig-font-small"
          @click="changeTab('postings')"
        />
        <IgButton
          class="ig-small ig-auto px-3 mx-3 mb-3"
          buttonName="tournaments"
          :textColor="
            selectedTab == 'tournaments' ? 'ig-white-text' : 'ig-gray-text'
          "
          :backgroundColor="selectedTab == 'tournaments' ? 'ig-gray' : ''"
          SmallFontSize="ig-font-small"
          @click="changeTab('tournaments')"
        />
      </div>
    </space-tab>

    <div class="m-16 md:m-40">
      <transition name="fade">
        <div v-if="selectedTab == 'home'">
          <play-now
            VideoTitle="title_play_now_box"
            VideoInfo="sub_title_play_now"
          ></play-now>
          <div
            class="flex justify-center flex-col lg:justify-between lg:flex-row"
          >
            <description
              descName="description"
              :descInfo="description"
              :descDate="created"
              :spaceMedia="spaceMedia"
              :dateIcon="require('@/assets/images/icons/date.svg')"
            ></description>
            <crew-members
              :crewData="crew"
              :membersData="members"
            ></crew-members>
          </div>
        </div>
      </transition>

      <transition name="fade">
        <div v-if="selectedTab == 'postings'">
          <add-posts></add-posts>
        </div>
      </transition>

      <transition name="fade">
        <div v-if="selectedTab == 'tournaments'">
          <tournament> </tournament>
        </div>
      </transition>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import SpaceTab from "@/components/space/spaceTab.vue";
import MySpaceHeader from "@/components/space/MySpaceHeader.vue";
import Description from "@/components/space/Description.vue";
import CrewMembers from "@/components/space/Crew-Members.vue";
import PlayNow from "@/components/space/PlayNowBox.vue";
import AddPosts from "@/components/space/Postings.vue";
import Tournament from "@/components/space/tournament/Tournament.vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  name: "MySapcePage",
  components: {
    MySpaceHeader,
    SpaceTab,
    Description,
    CrewMembers,
    PlayNow,
    AddPosts,
    Tournament,
    IgButton,
  },
  data: () => ({
    selectedTab: "home",
    name: "",
    icon: "",
    thumb: null,
    members_count: 0,
    description: "",
    created: "",
    members: [],
    crew: [],
    spaceMedia: [],
  }),
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    changeTab(tab: string) {
      this.selectedTab = tab;
    },
    async getSpaceData() {
      var response = await axios.get(
        this.apiUrl + "/space/" + this.$route.params.id,
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        }
      );
      if (response.data && response.data.results) {
        this.name = response.data.results[0].name;
        this.members_count = response.data.results[0].members_count;
        this.description = response.data.results[0].description;
        this.created = response.data.results[0].created;
        this.members = response.data.results[0].members;
        this.crew = response.data.results[0].crew;
        this.spaceMedia = response.data.results[0].spacemedia;
        this.icon = response.data.results[0].icon;
        this.thumb = response.data.results[0].thumb;
      }
    },
  },
  async created() {
    await this.getSpaceData();
  },
});
</script>

<style lang="scss" scoped>
.fade {
  &-enter-from {
    transform: translateY(50px);
  }
  &-enter-active {
    transition: all 0.8s ease-in-out;
  }
}
</style>
