
import { defineComponent } from "vue";
import SpaceTab from "@/components/space/spaceTab.vue";
import MySpaceHeader from "@/components/space/MySpaceHeader.vue";
import Description from "@/components/space/Description.vue";
import CrewMembers from "@/components/space/Crew-Members.vue";
import PlayNow from "@/components/space/PlayNowBox.vue";
import AddPosts from "@/components/space/Postings.vue";
import Tournament from "@/components/space/tournament/Tournament.vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  name: "MySapcePage",
  components: {
    MySpaceHeader,
    SpaceTab,
    Description,
    CrewMembers,
    PlayNow,
    AddPosts,
    Tournament,
    IgButton,
  },
  data: () => ({
    selectedTab: "home",
    name: "",
    icon: "",
    thumb: null,
    members_count: 0,
    description: "",
    created: "",
    members: [],
    crew: [],
    spaceMedia: [],
  }),
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    changeTab(tab: string) {
      this.selectedTab = tab;
    },
    async getSpaceData() {
      var response = await axios.get(
        this.apiUrl + "/space/" + this.$route.params.id,
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        }
      );
      if (response.data && response.data.results) {
        this.name = response.data.results[0].name;
        this.members_count = response.data.results[0].members_count;
        this.description = response.data.results[0].description;
        this.created = response.data.results[0].created;
        this.members = response.data.results[0].members;
        this.crew = response.data.results[0].crew;
        this.spaceMedia = response.data.results[0].spacemedia;
        this.icon = response.data.results[0].icon;
        this.thumb = response.data.results[0].thumb;
      }
    },
  },
  async created() {
    await this.getSpaceData();
  },
});
